* header {
  box-sizing: border-box;
}

header {
  font-family: Roboto;
  background-color: transparent;
  border-radius: 0 0 5px 5px;
  width: 100%;
  height: 60px;
  padding: 0 20px ;
  position: fixed;
  color: #111;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

header h2 {
  font-size: 1.6em;
  font-weight: lighter;
  margin: 0;
}
  header h2:hover {
    cursor: pointer;
  }

header ul {
    margin: 0px;
    padding: 0;
    font-size: 20px;
}

header ul li {
  padding: 2px 5px;
  cursor: pointer;
  font-size: 1.1em;
  list-style: none;
}

header ul li:hover, header ul li a:hover {
     background-color: #abcdef;
     border-radius: 5px;
     text-shadow: none;
     transition: .4s;
  }

  header a {
    color: inherit;
    text-decoration: none;
  }

  header a:visited {
    text-decoration: none;
    color: inherit;
  }


@media all and (max-width: 750px) {

  header {
  }

  .desktop-nav {
    display: none;
  }

}
@media all and (min-width: 750px) {

  #toggle-button {
     display: none;
  }

  header {

  }

  header ul {
    display: flex;
    justify-content: space-between;
  }

  .side-drawer {
    display: none;
  }

}
