.side-drawer {
  height: 100%;
  padding: 2em;
  background: white;
  box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  right: 0;
  width: 60%;
  max-width: 400px;
  z-index: 200;
  transform: translateX(00%);
  transition: transform 0.3s ease-out;
}

.side-drawer.open {
  transform: translateX(0);
}

.side-drawer ul {
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.side-drawer li {
  margin: 0.5rem 0;
}

.side-drawer a {
  color: #111;
  text-decoration: none;
  font-size: 1.2rem;
}

.side-drawer a:hover,
.side-drawer a:active {
  color: #abcdef;
}


@media (min-width: 750px) {
    .side-drawer {
        display: none;
    }
}
